/* CSS Document */
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


.app {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
}

/* ====================== Header ====================== */
.App-header {
  display: -ms-flexbox;
  display: flex;
  background-color: #222;
  width: 100%;
  height: 4rem;
  padding: 2rem;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}


button:focus {
  outline:0;
}

.hamburger {
  background-color: #FFF0;
  display: -ms-flexbox;
  display: flex;
  width: 10%;
  font-size: 1.5rem;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
      transform: translate(0, 0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  cursor: pointer;
  border: none;
  color: white;
}

.hamburger.open {
  width: 10%;
  font-size: 1.5rem;
  cursor: pointer;
  -webkit-transform: translate(250px, 0);
  -ms-transform: translate(250px, 0);
      transform: translate(250px, 0);
  border: none;
}

.App-title {
  width: 90%;
  font-size: 1.5rem;
}

.App-title.open {
  width: 90%;
  font-size: 1.5rem;
}

/* ====================== Locations List ====================== */

.location-menu {
  z-index: 10;
  background-color: black;
  height: 100%;
  width: 300px;
  position: absolute;
  -webkit-transform: translate(-300px, 0);
  -ms-transform: translate(-300px, 0);
      transform: translate(-300px, 0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.location-menu.open {
  width: 20%;
  height: 100%;
  position: absolute;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100%;
  background-color: black;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
      transform: translate(0, 0);
}

.menu-title {
  font-size: 1.5rem;
  text-align: center;
  padding: 1rem;
}

.location-filter {
  width: 90%;
  min-height: 48px;
  padding: 0;
  border-radius: 7px;
  text-align: center;
  margin: 0.5rem;
}

.location-list {
  list-style: none;
  font-size: 1rem;
  padding: 0;
}

.location-name {
  padding: 1rem;
  cursor: pointer;
}

.location-name.active {
  background-color: #3e8e41;
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}

/* ====================== Map ====================== */

.map {
  width: 100%;
  height: 100%;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.error-container {
  position: absolute;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 15rem;
}

.error-info {
  width: 100%;
  font-size: 2rem;
  color: #3e8e41;

}

.gm-style .gm-style-iw {
  background-color: #3e8e41;
  color: white;
  border-radius: 1rem;
}

.marker-title {
  font-weight: 900;
  font-size: 1rem;
}

a {
  color: white; 
}

.recommendations-title {
  font-weight: 900;
}

.recommendations-list {
  list-style: none;
  padding: 0;
}

.info {
  font-size: 0.5rem;
}

/* ====================== Breakpoints ====================== */

@media all and (max-width: 415px) {

/* Header */


  .App-title {
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
        transform: translate(0, 0);
  }

  .App-title.open {
    width: 90%;
    font-size: 1.5rem;
    -webkit-transform: translate(250px, 0);
    -ms-transform: translate(250px, 0);
        transform: translate(250px, 0);
    border: none;
  }

  .hamburger.open {
    -webkit-transform: translate(230px, 0);
    -ms-transform: translate(230px, 0);
        transform: translate(230px, 0);
  }

/* Locations List */


  .location-menu.open {
    width: 60%;
  }

  .location-name,
  .location-name.open {
    font-size: 1.2rem;
  }

  .error-container {
    padding-left: 0rem;
    width: 50%;
  }
  .error-info {
    font-size: 1rem;
  }

}

@media all and (min-width: 415px) and (max-width: 550px) {

/* Header */
  .App-title {
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
        transform: translate(0, 0);
  }

  .App-title.open {
    width: 90%;
    font-size: 1.5rem;
    -webkit-transform: translate(300px, 0);
    -ms-transform: translate(300px, 0);
        transform: translate(300px, 0);
    border: none;
  }

 .hamburger.open {
  -webkit-transform: translate(300px, 0);
  -ms-transform: translate(300px 0);
      transform: translate(300px 0);
 }

/* Locations List */

  .location-menu.open {
    width: 60%;
  }

  .location-name,
  .location-name.open {
    font-size: 1.2rem;
  }

}

@media all and (min-width: 550px) and (max-width: 712px) {
/* Header */
  .App-title {
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
        transform: translate(0, 0);
  }

  .App-title.open {
    width: 90%;
    font-size: 1.5rem;
    -webkit-transform: translate(250px, 0);
    -ms-transform: translate(250px, 0);
        transform: translate(250px, 0);
    border: none;
  }
  .hamburger.open {
    -webkit-transform: translate(250px, 0);
    -ms-transform: translate(250px, 0);
        transform: translate(250px, 0);
  } 

  .location-name,
  .location-name.open {
    font-size: 1.2rem;
  }

  .location-menu.open {
    width: 40%;
  }
}


@media all and (min-width: 712px) and (max-width: 800px){
.App-title {
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
        transform: translate(0, 0);
  }

  .App-title.open {
    width: 90%;
    font-size: 1.5rem;
    -webkit-transform: translate(300px, 0);
    -ms-transform: translate(300px, 0);
        transform: translate(300px, 0);
    border: none;
  }
  
  .hamburger.open {
    -webkit-transform: translate(350px, 0);
    -ms-transform: translate(350px, 0);
        transform: translate(350px, 0);
  }

  .location-menu.open {
    width: 30%;
  }
}

@media all and (min-width: 800px) {

  .hamburger.open {
    -webkit-transform: translate(280px, 0);
    -ms-transform: translate(280px, 0);
        transform: translate(280px, 0);
  }
}



